<template>
  <div class="min-h-screen flex items-center justify-center bg-[#f9f9f9]">
    <div class="w-full max-w-md bg-white rounded-3xl shadow-xl p-10 relative overflow-hidden">

      <!-- Header -->
      <div class="text-center mb-8">
        <h1 class="text-4xl font-extrabold text-[#e53935] tracking-wider">SCI</h1>
        <p class="text-gray-500 mt-2 text-lg">Create a new account</p>
      </div>

      <!-- Form -->
      <form @submit.prevent="handleSignUp" class="space-y-6">
        <!-- Name -->
        <div class="relative">
          <input v-model="name" type="text" required autocomplete="name"
            class="peer w-full rounded-xl border border-gray-300 bg-white px-4 pt-5 pb-2 text-gray-700 placeholder-transparent focus:border-[#e53935] focus:ring-2 focus:ring-[#e53935]/30 focus:outline-none transition"
            placeholder="Full Name" />
          <label class="absolute left-4 top-2.5 text-gray-400 text-sm transition-all 
                        peer-placeholder-shown:top-5 peer-placeholder-shown:text-gray-400 peer-placeholder-shown:text-base 
                        peer-focus:top-2.5 peer-focus:text-[#e53935] peer-focus:text-sm pointer-events-none">
            Full Name
          </label>
        </div>

        <!-- Email -->
        <div class="relative">
          <input v-model="email" type="email" required autocomplete="email"
            class="peer w-full rounded-xl border border-gray-300 bg-white px-4 pt-5 pb-2 text-gray-700 placeholder-transparent focus:border-[#e53935] focus:ring-2 focus:ring-[#e53935]/30 focus:outline-none transition"
            placeholder="Email" />
          <label class="absolute left-4 top-2.5 text-gray-400 text-sm transition-all 
                        peer-placeholder-shown:top-5 peer-placeholder-shown:text-gray-400 peer-placeholder-shown:text-base 
                        peer-focus:top-2.5 peer-focus:text-[#e53935] peer-focus:text-sm pointer-events-none">
            Email
          </label>
        </div>

        <!-- Password -->
        <div class="relative">
          <input :type="showPassword ? 'text' : 'password'" v-model="password" required
            class="peer w-full rounded-xl border border-gray-300 bg-white px-4 pt-5 pb-2 text-gray-700 placeholder-transparent focus:border-[#e53935] focus:ring-2 focus:ring-[#e53935]/30 focus:outline-none transition"
            placeholder="Password" />
          <label class="absolute left-4 top-2.5 text-gray-400 text-sm transition-all 
                        peer-placeholder-shown:top-5 peer-placeholder-shown:text-gray-400 peer-placeholder-shown:text-base 
                        peer-focus:top-2.5 peer-focus:text-[#e53935] peer-focus:text-sm pointer-events-none">
            Password
          </label>
          <!-- Eye toggle -->
          <span @click="showPassword = !showPassword" class="absolute right-4 top-3 text-gray-400 cursor-pointer select-none">
            {{ showPassword ? 'üôà' : 'üëÅÔ∏è' }}
          </span>
        </div>

        <!-- Confirm Password -->
        <div class="relative">
          <input :type="showConfirmPassword ? 'text' : 'password'" v-model="confirmPassword" required
            class="peer w-full rounded-xl border border-gray-300 bg-white px-4 pt-5 pb-2 text-gray-700 placeholder-transparent focus:border-[#e53935] focus:ring-2 focus:ring-[#e53935]/30 focus:outline-none transition"
            placeholder="Confirm Password" />
          <label class="absolute left-4 top-2.5 text-gray-400 text-sm transition-all 
                        peer-placeholder-shown:top-5 peer-placeholder-shown:text-gray-400 peer-placeholder-shown:text-base 
                        peer-focus:top-2.5 peer-focus:text-[#e53935] peer-focus:text-sm pointer-events-none">
            Confirm Password
          </label>
          <span @click="showConfirmPassword = !showConfirmPassword" class="absolute right-4 top-3 text-gray-400 cursor-pointer select-none">
            {{ showConfirmPassword ? 'üôà' : 'üëÅÔ∏è' }}
          </span>
        </div>

        <!-- Submit -->
        <button type="submit"
          class="w-full py-3 rounded-xl font-semibold text-white bg-[#e53935] shadow-md hover:bg-red-600 hover:scale-105 transform transition-all">
          Sign Up
        </button>
      </form>

      <!-- Footer -->
      <p class="mt-6 text-center text-gray-500 text-sm">
        Already have an account?
        <router-link to="/login" class="text-[#e53935] hover:underline font-medium">Sign In</router-link>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "SignUpPage",
  data() {
    return {
      name: "",
      email: "",
      password: "",
      confirmPassword: "",
      showPassword: false,
      showConfirmPassword: false
    };
  },
  methods: {
    handleSignUp() {
      if (!this.name || !this.email || !this.password || !this.confirmPassword) {
        alert("Please fill all fields");
        return;
      }
      if (this.password !== this.confirmPassword) {
        alert("Passwords do not match");
        return;
      }
      // Here you can call your API to register user
      alert("Signup successful! Redirecting to login...");
      this.$router.push("/login");
    }
  }
};
</script>

<style>
/* Card shadow */
.w-full.max-w-md {
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

/* Autofill fix */
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset;
  -webkit-text-fill-color: #111;
  transition: background-color 5000s ease-in-out 0s;
}
</style>
